[class|='confetti'] {
  position: absolute;
}

$colors: (
  var(--ion-color-danger),
  var(--ion-color-warning),
  var(--ion-color-success)
);

@for $i from 0 through 50 {
  $w: random(20);
  $l: random(100);
  .confetti-#{$i} {
    width: #{$w}px;
    height: #{$w * 0.4}px;
    background-color: nth($colors, random(3));
    top: -10%;
    left: unquote($l + '%');
    opacity: random() + 0.5;
    transform: rotate(#{random() * 360}deg);
    animation: drop-#{$i}
      unquote(4 + random() + 's')
      unquote(random() + 's')
      var(--animation-iteration, infinite);
  }

  @keyframes drop-#{$i} {
    100% {
      top: 110%;
      left: unquote($l + random(15) + '%');
    }
  }
}
